import React, { useEffect, useSate } from "react";

const FetchData = url => {
  const [state, setState] = useSate({ data: null, loading: true });

  useEffect(() => {
    setState({data: null, loading: true})
    fetch(url)
      .then(x => x.text())
      .then(y => setState({ data: y, loading: false }));
  }, [url]);

  return state;
};

export default FetchData;
